@import url(https://fonts.googleapis.com/css?family=Asap:400,700|Open+Sans:400,600);
$bg_color: #F5F5F5;
$header_bg: #fff;
$black_primary: rgba(0, 0, 0, 0.87);
$black_secondary: rgba(0, 0, 0, 0.54);
$black_disabled: rgba(0, 0, 0, 0.38);
$white_primary: rgba(255, 255, 255, 1);
$white_secondary: rgba(255, 255, 255, 0.7);
$primary_color: #E91E4C;
$accent_color: #00B8D4;
$accent_light_color: #B2EBF2;
$blue_gray_100: #ECEFF1;
body {
    font-family: 'Open Sans', sans-serif;
    background-color: $bg_color;
    color: $black_primary;
}

h1,
h2,
h3,
h4 {
    font-family: 'Asap', sans-serif;
    font-weight: 600;
    color: $primary_color;
}

a {
    color: $accent_color;
    text-decoration: none;
}

#header-wrap {
    background-color: $header_bg;
}

#header-wrap .mdl-navigation__link {
    color: $black_secondary;
    font-weight: 600;
    opacity: 1;
}

.farma__header-row {
    padding-left: 16px;
}

#hero {
    background-image: linear-gradient(-180deg, rgba(10, 30, 56, 0.00) 12%, rgba(6, 34, 62, 0.4) 61%, rgba(4, 35, 65, 0.8) 100%), url(../images/hero_bg.jpg);
    background-size: cover, cover;
    background-position: center top;
    position: relative;
    padding-top: 10em;
    padding-bottom: 1em;
    box-shadow: inset 0px -2px 4px rgba(0, 0, 0, 0.24);
    height: 50vh;
    h2 {
        max-width: 512px;
        margin: 0 auto 1em;
        font-size: 36px;
        font-weight: 600;
        color: #FFFFFF;
        line-height: 40px;
        text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.24);
        text-align: center;
    }
    .more2 {
        display: inline-block;
        margin: 0 auto;
        color: $white_primary;
        // text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.24);
        font-weight: 400;
        font-size: 16px;
        text-transform: uppercase;
        letter-spacing: 0.1;
        text-align: center;
        background: $accent_color;
        padding: 8px 20px;
        letter-spacing: 0.10ex;
        border-radius: 2px;
    }
}

#hero-content {
    position: absolute;
    text-align: center;
    bottom: 32px;
    left: 0;
    right: 0;
}


/* O seu medicamento em: */

#usage-wrap {
    background-color: $primary_color;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.usage__description {
    color: $white_primary;
    font-size: 20px;
    line-height: 28px;
    text-align: center;
    padding: 1em 4em;
}

#contact-wrap {
    background-color: $accent_color;
    h3 {
        color: $white_primary;
    }
}

.contact-cell {
    text-align: center;
}

.contact-form {
    padding: 24px;
    width: 100%;
}

.content-grid {
    max-width: 1280px;
}

.usage-item {
    text-align: center;
    img {
        border-radius: 50%;
        max-width: 100%;
    }
    h4 {
        color: $white_primary;
        font-size: 20px;
        line-height: 24px;
    }
    p {
        color: $white_primary;
        font-size: 16px;
        line-height: 20px;
        // padding: 0 16px;
    }
}

.phone {
    text-align: center;
}

.phone-img {
    max-width: 100%;
}

.mdl-cell {
    // background-color: rgba(0,0,0,0.2)
}

#farma-wrap {
    background-color: #eee;
}

.farma__description {
    text-align: center;
    h2 {
        font-size: 24px;
        text-align: center;
        color: $accent_color;
        margin-bottom: 1em;
    }
    p {
        font-size: 20px;
        line-height: 28px;
        text-align: center;
        padding: 0 4em;
    }
}

.farma__item {
    text-align: center;
    img {
        margin-bottom: 1em;
        border-radius: 50%;
        max-width: 100%;
    }
    p {
        font-size: 16px;
        color: $black_primary;
        line-height: 20px;
        margin-bottom: 8px;
    }
}


/**** Mobile layout ****/

@media (max-width: 839px) {
    #header-wrap .mdl-navigation__link {
        display: none;
    }
    .mdl-layout-title {
        margin: 0 auto;
    }
    .mdl-layout-spacer {
        flex-grow: 0;
    }
    .phone {
        display: none;
    }

    .usage__description {
        padding: 1em 0;
    }

    .farma__description p {
        padding: 0;
    }
}